@import '/src/App.scss';

.profile-image-edit {
    z-index: 10;
    @include main;
    @include row;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    .btns {
        @include col;
        position: absolute;
        bottom: 160px;
        button {
            font-size: 20px;
            width: 300px;
            height: 60px;
            border: none;
            background-color: white;
            color: $main-gray;
        }
        p {
            cursor: pointer;
            font-size: 20px;
            text-align: center;
            padding: 20px 40px;
            background: white;
            color: $main-gray;
        }
        button:nth-child(1) {
            border-bottom: 1px solid $main-gray;
            border-radius: 8px 8px 0 0;
        }
        button:last-child {
            border-radius: 0 0 8px 8px;
            border-top: 1px solid $main-gray;
        }
    }
    #image-file {
        display: none;
    }
}